"use client"

import { useEffect, useState } from "react"
import { useParams } from "next/navigation"
import { useContract } from "@/context/contract-context"
import { useSubgraph } from "@/context/graphql/queries" 
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Input } from "@/components/ui/input"
import { toast } from "sonner"
import { formatTimestamp, getStatusText, getRestrictionText, getVotingTypeText, getResultTypeText } from "@/lib/utils"
import CampaignResults from "./campaign-results"
import CandidateList from "./candidate-list"
import ProposalList from "./proposal-list"
import CountdownTimer from "./countdown-timer"

export default function CampaignDetailsPage() {
  const params = useParams()
  const campaignId = params.id ? Number(params.id) : 0
  const { contract, address, isConnected } = useContract()
  const { events, loading: subgraphLoading } = useSubgraph()

  // Campaign data states
  const [loading, setLoading] = useState(true)
  const [campaignInfo, setCampaignInfo] = useState(null)
  const [campaignStats, setCampaignStats] = useState(null)
  const [campaignTimeInfo, setCampaignTimeInfo] = useState(null)
  const [userRegistrationType, setUserRegistrationType] = useState(null)
  const [hasVoted, setHasVoted] = useState(false)
  const [activeTab, setActiveTab] = useState("info")
  const [passKey, setPassKey] = useState("")

  // Fetch campaign data
  useEffect(() => {
    if (!contract || !campaignId) return

    const fetchCampaignData = async () => {
      try {
        setLoading(true)

        // Fetch basic campaign info
        const info = await contract.getCampaignBasicInfo(campaignId)
        setCampaignInfo({
          votingType: Number(info.votingType),
          restriction: Number(info.restriction),
          resultType: Number(info.resultType),
          creator: info.creator,
          description: info.description,
        })

        // Fetch campaign stats
        const stats = await contract.getCampaignStats(campaignId)
        setCampaignStats({
          totalVotes: Number(stats.totalVotes),
          itemCount: Number(stats.itemCount),
          registeredVoterCount: Number(stats.registeredVoterCount),
        })

        // Fetch campaign time info
        const timeInfo = await contract.getCampaignTimeInfo(campaignId)
        setCampaignTimeInfo({
          status: Number(timeInfo.status),
          startTime: timeInfo.startTime,
          endTime: timeInfo.endTime,
        })

        // Check if user is registered and has voted
        if (isConnected && address) {
          const regType = await contract.getUserRegistrationType(campaignId, address)
          setUserRegistrationType(Number(regType))

          const voted = await contract.hasUserVoted(campaignId, address)
          setHasVoted(voted)
        }

        // Set initial active tab based on campaign status and user role
        setInitialActiveTab()
      } catch (error) {
        console.error("Error fetching campaign data:", error)
        toast.error("Failed to load campaign data")
      } finally {
        setLoading(false)
      }
    }

    fetchCampaignData()
  }, [contract, campaignId, address, isConnected])

  // Set initial active tab based on campaign status and user role
  const setInitialActiveTab = () => {
    if (!campaignInfo || !campaignTimeInfo) return

    const isCreator = address && campaignInfo.creator.toLowerCase() === address.toLowerCase()
    const now = Math.floor(Date.now() / 1000)
    const startTime = Number(campaignTimeInfo.startTime)
    const endTime = Number(campaignTimeInfo.endTime)

    if (isCreator) {
      // Creator always sees results
      setActiveTab("results")
    } else if (now < startTime) {
      // Before campaign starts
      if (campaignInfo.restriction !== 0) {
        // Not Open
        setActiveTab("register")
      } else {
        setActiveTab("info")
      }
    } else if (now >= startTime && now < endTime) {
      // During voting period
      if (userRegistrationType > 0) {
        setActiveTab("vote")
      } else {
        setActiveTab("info")
      }
    } else {
      // After campaign ends
      setActiveTab("results")
    }
  }

  // Check if user can vote
  const canVote = () => {
    if (!campaignTimeInfo || !isConnected) return false

    const now = Math.floor(Date.now() / 1000)
    const startTime = Number(campaignTimeInfo.startTime)
    const endTime = Number(campaignTimeInfo.endTime)

    return now >= startTime && now < endTime && userRegistrationType > 0 && !hasVoted
  }

  // Check if user can register
  const canRegister = () => {
    if (!campaignTimeInfo || !campaignInfo || !isConnected) return false

    const now = Math.floor(Date.now() / 1000)
    const startTime = Number(campaignTimeInfo.startTime)

    return (
      now < startTime &&
      campaignInfo.restriction !== 0 && // Not Open
      userRegistrationType === 0 // Not registered
    )
  }

  // Handle vote casting
  const handleVote = async (itemId) => {
    if (!contract || !isConnected) {
      toast.error("Please connect your wallet")
      return
    }

    try {
      setLoading(true)

      let tx
      if (campaignInfo.votingType === 0) {
        // Candidate-based
        tx = await contract.voteForCandidate(campaignId, itemId)
      } else {
        // Proposal-based
        tx = await contract.voteForProposal(campaignId, itemId)
      }

      await tx.wait()
      setHasVoted(true)
      toast.success("Vote cast successfully!")

      // Refresh campaign stats
      const stats = await contract.getCampaignStats(campaignId)
      setCampaignStats({
        totalVotes: Number(stats.totalVotes),
        itemCount: Number(stats.itemCount),
        registeredVoterCount: Number(stats.registeredVoterCount),
      })

      setActiveTab("results")
    } catch (error) {
      console.error("Error casting vote:", error)
      toast.error("Failed to cast vote")
    } finally {
      setLoading(false)
    }
  }

  // Handle registration
  const handleRegister = async () => {
    if (!contract || !isConnected) {
      toast.error("Please connect your wallet")
      return
    }

    if (!passKey && campaignInfo.restriction === 2) {
      // Registration Required
      toast.error("Please enter the pass key")
      return
    }

    try {
      setLoading(true)

      const tx = await contract.registerToVote(campaignId, passKey)
      await tx.wait()

      const regType = await contract.getUserRegistrationType(campaignId, address)
      setUserRegistrationType(Number(regType))

      toast.success("Registration successful!")

      // Refresh campaign stats
      const stats = await contract.getCampaignStats(campaignId)
      setCampaignStats({
        totalVotes: Number(stats.totalVotes),
        itemCount: Number(stats.itemCount),
        registeredVoterCount: Number(stats.registeredVoterCount),
      })

      setActiveTab("info")
    } catch (error) {
      console.error("Error registering:", error)
      toast.error("Failed to register")
    } finally {
      setLoading(false)
    }
  }

  if (loading || !campaignInfo || !campaignTimeInfo) {
    return (
      <div className="container mx-auto py-8 px-4">
        <div className="space-y-4">
          <Skeleton className="h-12 w-3/4" />
          <Skeleton className="h-6 w-1/2" />
          <Skeleton className="h-64 w-full" />
        </div>
      </div>
    )
  }

  const isCreator = address && campaignInfo.creator.toLowerCase() === address.toLowerCase()
  const now = Math.floor(Date.now() / 1000)
  const startTime = Number(campaignTimeInfo.startTime)
  const endTime = Number(campaignTimeInfo.endTime)

  // Determine countdown message
  let countdownMessage = ""
  let targetTime = 0

  if (now < startTime) {
    countdownMessage = "Voting starts in:"
    targetTime = startTime
  } else if (now < endTime) {
    countdownMessage = "Voting ends in:"
    targetTime = endTime
  } else {
    countdownMessage = "Campaign ended"
  }

  return (
    <div className="container mx-auto py-8 px-4">
      <div className="mb-8">
        <h1 className="text-3xl font-bold capitalize mb-2">{campaignInfo.description}</h1>

        <div className="flex flex-wrap gap-2 mb-4">
          <Badge variant="outline">{getStatusText(campaignTimeInfo.status)}</Badge>
          <Badge variant="outline">{getVotingTypeText(campaignInfo.votingType)}</Badge>
          <Badge variant="outline">{getRestrictionText(campaignInfo.restriction)}</Badge>
          <Badge variant="outline">{getResultTypeText(campaignInfo.resultType)}</Badge>
        </div>

        <div className="bg-muted p-4 rounded-lg">
          <h2 className="text-lg font-medium mb-2">{countdownMessage}</h2>
          {now < endTime ? <CountdownTimer targetTime={targetTime} /> : <p>Final results are available</p>}
        </div>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="info">Info</TabsTrigger>
          {canRegister() && <TabsTrigger value="register">Register</TabsTrigger>}
          {canVote() && <TabsTrigger value="vote">Vote</TabsTrigger>}
          <TabsTrigger value="results">Results</TabsTrigger>
        </TabsList>

        <TabsContent value="info" className="mt-4">
          <Card>
            <CardHeader>
              <CardTitle>Campaign Information</CardTitle>
              <CardDescription>Created by {campaignInfo.creator}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <h3 className="font-medium">Time Frame</h3>
                <p>Start: {formatTimestamp(campaignTimeInfo.startTime)}</p>
                <p>End: {formatTimestamp(campaignTimeInfo.endTime)}</p>
              </div>

              <div>
                <h3 className="font-medium">Statistics</h3>
                <p>Total Votes: {campaignStats?.totalVotes || 0}</p>
                <p>Registered Voters: {campaignStats?.registeredVoterCount || 0}</p>
                <p>
                  {campaignInfo.votingType === 0
                    ? `Candidates: ${campaignStats?.itemCount || 0}`
                    : `Proposals: ${campaignStats?.itemCount || 0}`}
                </p>
              </div>

              {isConnected && (
                <div>
                  <h3 className="font-medium">Your Status</h3>
                  <p>
                    {userRegistrationType > 0
                      ? `Registered as: ${userRegistrationType === 1 ? "Voter" : "Candidate"}`
                      : "Not registered"}
                  </p>
                  <p>{hasVoted ? "You have voted" : "You have not voted yet"}</p>
                </div>
              )}
            </CardContent>
            <CardFooter>
              {!isConnected && <p className="text-muted-foreground">Connect your wallet to participate</p>}
            </CardFooter>
          </Card>
        </TabsContent>

        {canRegister() && (
          <TabsContent value="register" className="mt-4">
            <Card>
              <CardHeader>
                <CardTitle>Register to Vote</CardTitle>
                <CardDescription>Register to participate in this campaign</CardDescription>
              </CardHeader>
              <CardContent>
                {campaignInfo.restriction === 2 && (
                  <div className="mb-4">
                    <label className="block text-sm font-medium mb-1">Pass Key</label>
                    <Input
                      type="text"
                      placeholder="Enter pass key"
                      value={passKey}
                      onChange={(e) => setPassKey(e.target.value)}
                    />
                  </div>
                )}
                <p className="text-sm text-muted-foreground mb-4">
                  By registering, you will receive an NFT that represents your voting right.
                </p>
              </CardContent>
              <CardFooter>
                <Button onClick={handleRegister} disabled={loading}>
                  {loading ? "Registering..." : "Register"}
                </Button>
              </CardFooter>
            </Card>
          </TabsContent>
        )}

        {canVote() && (
          <TabsContent value="vote" className="mt-4">
            <Card>
              <CardHeader>
                <CardTitle>Cast Your Vote</CardTitle>
                <CardDescription>Select an option to vote</CardDescription>
              </CardHeader>
              <CardContent>
                {campaignInfo.votingType === 0 ? (
                  <CandidateList campaignId={campaignId} contract={contract} onVote={handleVote} loading={loading} />
                ) : (
                  <ProposalList campaignId={campaignId} contract={contract} onVote={handleVote} loading={loading} />
                )}
              </CardContent>
            </Card>
          </TabsContent>
        )}

        <TabsContent value="results" className="mt-4">
          <Card>
            <CardHeader>
              <CardTitle>Results</CardTitle>
              <CardDescription>{campaignTimeInfo.status === 2 ? "Final results" : "Current standings"}</CardDescription>
            </CardHeader>
            <CardContent>
              <CampaignResults
                campaignId={campaignId}
                contract={contract}
                votingType={campaignInfo.votingType}
                status={campaignTimeInfo.status}
              />
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
